





















import { AxiosResponse } from 'axios';
import { Component, Vue } from 'vue-property-decorator';

// Components
import PageLayout from '../components/PageLayout.vue';
import Search from '../components/Search.vue';
import ResultCard from '../components/ui/ResultCard.vue';
import ResultCardRow from '../components/ui/ResultCardRow.vue';

// Repository
import repository from '../repositories/repository';

@Component({
  components: {
    ResultCard,
    PageLayout,
    Search,
    ResultCardRow,
  },
})
export default class HomePage extends Vue {
  shareLink?: string = '';
  webRequest?: any = {
    status: 200,
    errors: {},
    data: {
      url: '',
      response: {
      },
      request: {
      },
    },
  };
  results: any = {};
  handleSubmit($event: any) {
    this.prepareWebRequest($event);
    repository.submitRequest(this.webRequest).then((response: AxiosResponse<any>) => {
      const id: string = response.data.uri.split(/\//).reverse()[0];
      this.shareLink = `${window.location.origin}/#/${id}`;
      this.retrieveRequest(id);
    });
  }
  prepareWebRequest(request: any) {
    this.webRequest = {
      status: 200,
      errors: {},
      data: {
        url: request.url,
        response: {
          http_version_string: 'HTTP/1.1',
          reason: 'OK',
          request_date: 'Thu, 23 Jan 2020',
          request_server: 'gws',
          status_code: 200,
          url: 'http://goole.it',
        },
        request,
      },
    };
  }
  retrieveRequest(id: string) {
    repository.getRequest(id).then((response: AxiosResponse<any>) => {
      this.results = response.data;
    });
  }
}
